		margin-left: 10px;
		margin-top: 20px;
		cursor: pointer;
	}
	
	.fundRun-box {
		/*min-width: 1100px;*/
		height: auto;
		padding-right: 16px;
	}
	
	.fundRun-top-box {
		width: 100%;
		height: 120px;
