			position: absolute;
			left: 30px;
			bottom: 24px;
		}
	}
	
	.fundRun-body-box {
		width: 100%;
		background-color: #ffffff;
		// margin-top: 20px;
		// box-shadow: 0px 0px 40px 0px rgba(215, 225, 246, 1);
		padding: 0px 0px 0px;
		.fundRun-body-title {
