			}
		}
		.fundRun-body-nav {
			border-top: 1px solid #F1F1F1;
			margin-top: 16px;
			.fundRun-body-nav-item {
				float: left;
				display: inline-block;
				margin-right: 20px;
				position: relative;
				top: -1px;
				border-top: 1px solid #F1F1F1;
