			}
		}
		.fundRun-body-tab-box {
			margin-top: 10px; // .fundRun-body-tab-btn-box {
			//   margin-bottom: 10px;
			//   .fundRun-body-tab-btn {
			//     line-height: 23px;
			//     font-size: 14px;
			//     padding: 0px 6px;
			//     &>span {
			//       display: inline-block;
			//       width: 14px;
			//       height: 14px;
			//       float: left;
			//       margin-top: 5px;
			//       margin-right: 4px;
			//       background: url('../../images/projectpermissions_icon1.png') no-repeat;
			//       background-size: 100%;
			//     }
			//   }
			// }
			.fundRun-body-tab-title {
				margin-bottom: 10px;
				.cricle {
					display: inline-block;
					width: 6px;
					height: 6px;
					background-color: #3C98E8;
					border-radius: 3px;
					margin-right: 2px;
					margin-left: 20px;
					position: relative;
					top: -2px;
				}
				.fundRun-body-tab-title-name {
					font-size: 14px;
					font-weight: bold;
					color: #35383A;
				}
			}
			.table-normal {
				border: none;
			} // .table-normal>.noColspanTr:hover{
			//   background-color: #F9FCFF;
			// }
			.table-normal>.noColspanTr>th {
				text-align: left;
				color: #35383A;
				font-weight: 500;
				background-color: #F6FBFF;
				border: none;
			}
			.table-normal>.noColspanTr>td {
				text-align: left;
				border-right: none;
				position: relative;
				&>span {
					color: #6F7071;
					font-size: 14px;
				}
				input {
					width: 110px;
					height: 24px;
					border: 1px solid #F1F1F1;
