<template>
  <div>
    <div class="fundRun-body-box">
      <div class="fundRun-body-title clear">
        各年份已投项目报表
      </div>
      <div class="fundRun-body-tab-box">
        <div class="fundrun_reportlist">
          <div class="fundrun_reportitem" v-for="(item,index) in reportList">
            <div class="iconfont icon-wendang1"></div>
            <div class="fundrun_reportitem_text" v-if="index!=reportEditId">{{item.name}}</div>
            <div class="fundrun_reportitem_inputbox" v-if="index==reportEditId">
              <el-input size="mini" class="fundrun_reportitem_input module_ckmx_td_input" v-model="item.name"></el-input>
            </div>
            <div class="fundrun_reportitem_btn">查看</div>
            <div class="fundrun_reportitem_btn" v-if="index==reportEditId" @click="saveReport">保存</div>
            <div class="fundrun_reportitem_btn" v-if="index!=reportEditId" @click="editReport(index)">编辑</div>
            <div class="fundrun_reportitem_btn" @click="deleteReport(index)">删除</div>
          </div>
          <div class="fundrun_reportitem" v-if="reportEditId==-1">
            <div class="iconfont icon-wendang1"></div>
            <div class="fundrun_reportitem_inputbox">
              <el-input size="mini" class="fundrun_reportitem_input module_ckmx_td_input" v-model="newReport.name" placeholder="请输入报表名称"></el-input>
            </div>
            <div class="fundrun_reportitem_btn" @click="addReport">新增</div>
            <div class="fundrun_reportitem_btn" @click="cancelAdd">取消</div>
          </div>
        </div>
        <div class="fundrun_report_btnbox">
          <div class="fundrun_report_btn" @click="createReport">
            <span class="iconfont icon-add"></span> 新增年度报表
          </div>
        </div>
      </div>
    </div>
    <!-- 新建/编辑文件夹弹窗 -->
    <el-dialog :title="createReportDialogTitle" :visible.sync="createReportDialog" width="570px" center :append-to-body='true' class="dialog-content-movefile applyfun-modal">
      <div class="dialog_content">
        <div class="form-group margin-bottom-none">
          <div class="form-group-inputboxgroup clear">
            <div class="form-group-inputbox">
              <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>报表名称</h2>
              <div class="form-group-inputbox-selectbox select-normal-40 left">
                <el-input v-model="newFolderName">
                </el-input>
              </div>
              <span class="form-group-inputbox-error"></span>
            </div>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog_footer clear">
        <button class="dialog-btn-submit marginl_10 btn-blue" @click="createReportDialogClick">确认</button>
        <button class="dialog-btn-submit btn-blue-border" @click="createReportDialog=false">取消</button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      createReportDialogTitle: '新增年度报表',
      createReportDialog: false,
      newFolderName: '',
      reportEditId: -2,
      reportList: [{
        name: '2018年度已投项目报表'
      }],
      newReport: {
        name: ''
      }
    }
  },
  components: {},
  props: ['data'], //type=>1出款 2回款
  created() {},
  computed: {},
  mounted() {

  },
  methods: {
    editReport(index) {
      this.reportEditId = index;
    },
    cancelAdd() {
      this.reportEditId = -2;
      this.newReport.name = '';
    },
    addReport() {
      if (this.newReport.name === '') {
        dialogConfirm('info', '报表名称不能为空')
      } else {
        let newr = JSON.parse(JSON.stringify(this.newReport));
        this.reportList.push(newr);
        this.reportEditId = -2;
        this.newReport.name = '';
      }
    },
    saveReport() {
      this.reportEditId = -2
    },
    deleteReport(index) {
      dialogConfirmHasbtn('确定删除吗？').then(res => {
        this.reportList.splice(index, 1);
      }).catch(() => {})

    },
    createReportDialogClick() {

    },
    createReport() {
      this.reportEditId = -1;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/fund.scss';
@import '../../style/modal.scss';
.fundRun-body-title {
  font-size: 16px;
  font-weight: bold;
  line-height: 30px;
}

.fundrun_reportitem {
  display: flex;
  align-items: center; //max-width: 1000px;
  overflow: hidden;
  height: 40px;
  padding: 0px 10px;
  &:hover {
    background-color: #F9FCFF;
    .fundrun_reportitem_text {
      color: #3C98E8;
    }
  }
  .iconfont {
    font-size: 20px;
    color: #3C98E8;
    margin-right: 10px;
    flex-shrink: 0;
  }
  .fundrun_reportitem_text {
    font-size: 14px;
    flex-grow: 1;
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #35383A;
  }
  .fundrun_reportitem_inputbox {
    flex-grow: 1;
    .fundrun_reportitem_input {
      max-width: 300px;
      width: 100%;
    }
  }
  .fundrun_reportitem_btn {
    font-size: 14px;
    flex-shrink: 0;
    margin-left: 10px;
    color: #3C98E8;
    cursor: pointer;
  }
}

.fundrun_report_btnbox {
  display: flex;
  justify-content: center;
  margin-top: 20px; //max-width: 1000px;
}

.fundrun_report_btn {
  cursor: pointer;
  border: 1px solid #3C98E8;
  color: #3C98E8;
  display: flex;
  height: 30px;
  padding: 0px 10px;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  transition: all 0.3s;
  &:hover {
    background: #3C98E8;
    color: #fff;
    .iconfont {
      color: #fff;
    }
  }
  .iconfont {
    color: #3C98E8;
    margin-right: 5px;
    font-size: 20px;
    transition: all 0.3s;
  }
}

</style>



